/* Normal Font Script */
/* ------------------ */

.header {
    display: flex;
    align-items: center;
    color: #fff;
    background-color: #000;
    top: 0;
    position: -webkit-sticky;
    position: sticky;
    z-index: 2000;
}

.header__left {
    flex: 0.4 1;


}

.header__left >h1{
    text-align: center;
    font-family: 'McLaren', cursive;
    letter-spacing: 1px;
    padding: 10px 0px;
}

.header__left > h1 > span{
    color:#FFA500;
}

.header__right{
    display: flex;
    flex:0.6 1;
    justify-content: space-evenly;
    align-items: center;
}


.header__right > a > h4{
    font-size: 18px;
    font-family: 'Comic Neue', cursive;
    /* cursor: pointer; */
    padding: 10px 0;
    letter-spacing: 1px;
    transition: -webkit-transform 200ms ease-in-out;
    transition: transform 200ms ease-in-out;
    transition: transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
}

.header__right > a > h4:hover{
  -webkit-transform: scale(1.09);
          transform: scale(1.09);
    background-color: #FFA500;
    color: #000;
    padding: 5px 20px;
    border-radius: 2px;
    letter-spacing: 1px;
    font-family: 'Comic Neue', cursive;
    font-size: 18px;
}

.header__joinbtn{
    background-color: #FFA500;
    padding: 10px 15px;
    border-radius: 8px;
    font-family: 'Comic Neue', cursive;
    font-size: 18px;
    font-weight: bold;
}

.header__joinbtn:hover{
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
    background-color: #FFF;
    color: #FFA500;
/*    
    font-family: 'Comic Neue', cursive;
    font-size: 18px; */
}


