.topContent {
    color: #fff;
position: relative;
}

.topContnet__container {
    position: absolute;
    top: 300px;
    right: 200px;
}

.topContnet__container > p {
    letter-spacing: 1px;
    text-align: left;
    padding: 15px 3px 0px 0px;
}

.topContnet__container > a > button {
    margin-top: 30px;
    margin-bottom: 30px;
    text-align: left;
    margin-left: 5px;
    border: none;
    border-radius: 2px;
    outline: none;

} 

.topContnet__container > h1{
    font-family: 'McLaren', cursive;
    font-size: 50px;

}

.topContent__downloadBtn{
    color: #fff;
    background-color: #FFA500;
    font-size: 14px;
    padding: 10px 30px;  
    font-family: 'Comic Neue', cursive;
    font-weight: 300px;
}

.topContent__workBtn{
    color: #fff;
    border: 1px solid #FFF !important;
    background-color: transparent;
    padding: 9px 45px;
    font-family: 'Comic Neue', cursive;
    font-weight: 300px;
    
}

.topContent__workBtn:hover{

color: #000;
background-color: #fff;
font-weight: bold !important;

}
